<template>
  <div class="main">
        <div class="box">
          <el-form
            inline
            :model="form"
            label-width="130px"
            ref="Form"
            label-position="right"
            :rules="rules"
          >
            <div class="boxTitle"></div>
            <el-form-item label="">
              <el-input size="small" v-model="form.userName" disabled></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input size="small" v-model="form.userIdentity" disabled></el-input>
            </el-form-item>
            <el-form-item label="" prop="userSex" >
              <el-radio-group v-model="form.userSex">
                <el-radio label="" value=""></el-radio>
                <el-radio label="" value=""></el-radio>
              </el-radio-group>
            </el-form-item>
            <el-form-item label="" prop="userFamousRaceId" >
              <el-select size="small" v-model="form.userFamousRaceId" filterable placeholder="">
                <el-option
                  v-for="item in userFamousRaceList"
                  :key="item.famousRaceId"
                  :label="item.famousRaceName"
                  :value="item.famousRaceId"
                ></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="" prop="userBirthday" >
              <el-date-picker
                size="small"
                v-model="form.userBirthday"
                type="date"
                placeholder=""
                value-format="yyyy/MM/dd"
                format="yyyy  MM  dd "
              ></el-date-picker>
            </el-form-item>
            <el-form-item label="" prop="accountCharacterId" >
              <el-select
                size="small"
                v-model="form.accountCharacterId"
                filterable
                placeholder=""
              >
                <el-option
                  v-for="item in accountCharacterList"
                  :key="item.accountCharacterId"
                  :label="item.accountCharacterName"
                  :value="item.accountCharacterId"
                ></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="" prop="userMiddleSchool" >
              <el-input size="small" v-model="form.userMiddleSchool" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="" style="display:block" prop="fundingCenterId" >
              <el-select
                size="small"
                style="width:690px"
                v-model="form.fundingCenterId"
                filterable
                placeholder=""
              >
                <el-option
                  v-for="item in fundingCenterList"
                  :key="item.fundingCenterId"
                  :label="item.fundingCenterName"
                  :value="item.fundingCenterId"
                ></el-option>
              </el-select>
            </el-form-item>
            <el-form-item
              label=""
              prop="userPermanentAddress"
              style="display:block"
              required
            >
              <div class="selectGround">
                <el-select
                  size="small"
                  v-model="form.provinceId"
                  filterable
                  placeholder=""
                  @change="getCity('provinceId')"
                >
                  <el-option
                    v-for="item in provinceList"
                    :key="item.provinceId"
                    :label="item.provinceName"
                    :value="item.provinceId"
                  ></el-option>
                </el-select>
                <el-select
                  size="small"
                  v-model="form.cityId"
                  filterable
                  placeholder=""
                  @change="getArea('cityId')"
                >
                  <el-option
                    v-for="item in cityList"
                    :key="item.cityId"
                    :label="item.cityName"
                    :value="item.cityId"
                  ></el-option>
                </el-select>
                <el-select size="small" v-model="form.areaId" filterable placeholder="">
                  <el-option
                    v-for="item in areaList"
                    :key="item.areaId"
                    :label="item.areaName"
                    :value="item.areaId"
                  ></el-option>
                </el-select>
              </div>
              <el-input
                style="width:690px"
                size="small"
                v-model="form.userPermanentAddress"
                placeholder=""
              ></el-input>
            </el-form-item>
            <el-form-item label="" prop="userBankCardId" >
              <el-input size="small" type="number" v-model="form.userBankCardId" placeholder=""></el-input>
            </el-form-item>
            <div class="boxTitle"></div>
            <el-form-item label="" style="display:block" >
              <el-input size="small" type="number" v-model="form.userPhone" placeholder="" disabled></el-input>
            </el-form-item>
            <el-form-item prop="userPostalCode" label="" >
              <el-input size="small" type="number" v-model="form.userPostalCode" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input size="small" v-model="form.userMailBox" placeholder=""></el-input>
            </el-form-item>
            <el-form-item prop="userQq" label="QQ" >
              <el-input size="small" type="number" v-model="form.userQq" placeholder="QQ"></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input size="small" v-model="form.userWeChat" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input
                size="small"
                style="width:690px"
                v-model="form.userPostalAddress"
                placeholder=""
              ></el-input>
            </el-form-item>
            <el-form-item prop="userFamilyPhone" label="" style="display:block" >
              <el-input size="small" v-model="form.userFamilyPhone" placeholder=""></el-input>
            </el-form-item>

            <div class="boxTitle"></div>
            <el-form-item prop="userContactName" label="" >
              <el-input size="small" v-model="form.userContactName" placeholder=""></el-input>
            </el-form-item>
            <el-form-item prop="userContactPhone" label="" >
              <el-input size="small" type="number" v-model="form.userContactPhone" placeholder=""></el-input>
            </el-form-item>
            <el-form-item prop="userContactIdentity" label="" >
              <el-input size="small" v-model="form.userContactIdentity" placeholder=""></el-input>
            </el-form-item>
            <div class="boxTitle"></div>
            <el-form-item prop="userUniversity" label="" required style="display:block">
              <el-input size="small" v-model="form.userUniversity" placeholder=""></el-input>
            </el-form-item>
            <el-form-item prop="userDepartment" label="" >
              <el-input size="small" v-model="form.userDepartment" placeholder=""></el-input>
            </el-form-item>
            <el-form-item prop="userMajor" label="" >
              <el-input size="small" v-model="form.userMajor" placeholder=""></el-input>
            </el-form-item>
            <el-form-item prop="userEducation" label="" >
              <el-select size="small" v-model="form.userEducation" filterable placeholder="">
                <el-option
                  v-for="(item,index) in userEducationList"
                  :key="index"
                  :label="item"
                  :value="item"
                ></el-option>
              </el-select>
            </el-form-item>
            <el-form-item prop="userEnrollmentYear" label="" >
              <el-date-picker
                prefix-icon
                size="small"
                v-model="form.userEnrollmentYear"
                type="year"
                placeholder=""
                value-format="yyyy"
                format="yyyy "
              ></el-date-picker>
            </el-form-item>
            <el-form-item prop="userStudentId" label="">
              <el-input size="small" v-model="form.userStudentId" placeholder=""></el-input>
            </el-form-item>
            <el-form-item prop="userMajorCategory" label="" >
              <el-select
                size="small"
                v-model="form.userMajorCategory"
                filterable
                placeholder=""
              >
                <el-option
                  v-for="(item,index) in userMajorCategoryList"
                  :key="index"
                  :label="item"
                  :value="item"
                ></el-option>
              </el-select>
            </el-form-item>
            <el-form-item prop="userEducationalSystem" label="" >
              <el-select
                size="small"
                v-model="form.userEducationalSystem"
                filterable
                placeholder=""
              >
                <el-option
                  v-for="(item,index) in userEducationalSystemList"
                  :key="index"
                  :label="item"
                  :value="item"
                ></el-option>
              </el-select>
            </el-form-item>
            <el-form-item prop="userGraduationTime" label="" >
              <el-date-picker
                size="small"
                v-model="form.userGraduationTime"
                type="date"
                placeholder=""
                value-format="yyyy/MM/dd"
                format="yyyy  MM  dd "
              ></el-date-picker>
            </el-form-item>
            <div class="boxTitle"></div>
            <el-form-item prop="guardianName" label="" >
              <el-input size="small" v-model="form.guardianName" placeholder=""></el-input>
            </el-form-item>
            <el-form-item prop="guardianIdentity" label="" >
              <el-input size="small" type="number" v-model="form.guardianIdentity" placeholder=""></el-input>
            </el-form-item>
            <el-form-item
              prop="guardianPermanentAddress"
              label=""
              style="display:block"
              required
            >
              <div class="selectGround">
                <el-select
                  size="small"
                  v-model="form.guardianProvinceId"
                  filterable
                  placeholder=""
                  @change="getCity('guardianProvinceId')"
                >
                  <el-option
                    v-for="item in provinceList"
                    :key="item.provinceId"
                    :label="item.provinceName"
                    :value="item.provinceId"
                  ></el-option>
                </el-select>
                <el-select
                  size="small"
                  v-model="form.guardianCityId"
                  filterable
                  placeholder=""
                  @change="getArea('guardianCityId')"
                >
                  <el-option
                    v-for="item in cityList"
                    :key="item.cityId"
                    :label="item.cityName"
                    :value="item.cityId"
                  ></el-option>
                </el-select>
                <el-select
                  size="small"
                  v-model="form.guardianAreaId"
                  filterable
                  placeholder=""
                >
                  <el-option
                    v-for="item in areaList"
                    :key="item.areaId"
                    :label="item.areaName"
                    :value="item.areaId"
                  ></el-option>
                </el-select>
              </div>
              <el-input
                style="width:690px"
                size="small"
                v-model="form.guardianPermanentAddress"
                placeholder=""
              ></el-input>
            </el-form-item>
            <el-form-item prop="guardianPostalCode" label="" >
              <el-input size="small" type="number" v-model="form.guardianPostalCode" placeholder=""></el-input>
            </el-form-item>
            <el-form-item prop="guardianTel" label="" >
              <el-input size="small" type="number" v-model="form.guardianTel" placeholder=""></el-input>
            </el-form-item>
            <el-form-item prop="guardianHealth" label="" >
              <el-radio-group v-model="form.guardianHealth">
                <el-radio label="" value=""></el-radio>
                <el-radio label="" value=""></el-radio>
              </el-radio-group>
            </el-form-item>
            <el-form-item prop="relation" label="" >
              <el-input size="small" v-model="form.relation" placeholder=""></el-input>
            </el-form-item>
            <el-form-item class="submit">
               <el-button type="primary" @click="submit"></el-button>
            </el-form-item>
          </el-form>
         
  </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      accountCharacterList: [],
      fundingCenterList: [],
      provinceList: [],
      cityList: [],
      areaList: [],
      userEducationList: ["", "", "", "", ""],
      userEducationalSystemList: ["3", "4", "6", "8"],
      userMajorCategoryList: [
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        ""
      ],
      userFamousRaceList: [],
      form: {
        userName: "",
        userIdentity: "",
        userSex: "",
        userBirthday: "",
        userFamousRaceId: "",
        userMiddleSchool: "",
        accountCharacterId: "",
        fundingCenterId: "",
        provinceId: "",
        cityId: "",
        areaId: "",
        userPermanentAddress: "",
        userBankCardId: "",

        userPhone: "",
        userPostalCode: "",
        userMailBox: "",
        userQq: "",
        userWeChat: "",
        userPostalAddress: "",
        userFamilyPhone: "",

        userContactName: "",
        userContactPhone: "",
        userContactIdentity: "",

        userUniversity: "",
        userDepartment: "",
        userMajor: "",
        userEducation: "",
        userEnrollmentYear: "",
        userStudentId: "",
        userMajorCategory: "",
        userEducationalSystem: "",
        userGraduationTime: "",

        guardianName: "",
        guardianIdentity: "",
        guardianProvinceId: "",
        guardianCityId: "",
        guardianAreaId: "",
        guardianPermanentAddress: "",
        guardianPostalCode: "",
        guardianHealth: "",
        guardianTel: "",
        relation: ""
      },
      rules: {
        userSex: [{ required: true, message: "", trigger: "blur" }],
        userBirthday: [
          { required: true, message: "", trigger: "blur" }
        ],
        userFamousRaceId: [
          { required: true, message: "", trigger: "blur" }
        ],
        userMiddleSchool: [
          { required: true, message: "", trigger: "blur" }
        ],
        accountCharacterId: [
          { required: true, message: "", trigger: "blur" }
        ],
        fundingCenterId: [
          { required: true, message: "", trigger: "blur" }
        ],
        userPermanentAddress: [
          { required: true, message: "", trigger: "blur" }
        ],
        userBankCardId: [
          { required: true, message: "", trigger: "blur" }
        ],

        userPostalCode: [
          { required: true, message: "", trigger: "blur" }
        ],
        userQq: [{ required: true, message: "QQ", trigger: "blur" }],
        userFamilyPhone: [
          { required: true, message: "", trigger: "blur" },
          {
            validator: function(rule, value, callback) {
              if (/^((0\d{2,3}-\d{7,8})|(1[34578]\d{9}))$/.test(value) == false) {
                callback(new Error(""));
              } else {
                callback();
              }
            },
            trigger: "blur"
          }
        ],

        userContactName: [
          { required: true, message: "", trigger: "blur" }
        ],
        userContactPhone: [
          { required: true, message: "", trigger: "blur" },
          {
            validator: function(rule, value, callback) {
              if (/^((0\d{2,3}-\d{7,8})|(1[34578]\d{9}))$/.test(value) == false) {
                callback(new Error(""));
              } else {
                callback();
              }
            },
            trigger: "blur"
          }
        ],
        userContactIdentity: [
          { required: true, message: "", trigger: "blur" },
          { min: 15, max: 18, message: "", trigger: "blur" },
          {
            required: true,
            pattern: /(^[1-9]\d{5}\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{2}$)|(^[1-9]\d{5}(18|19|([23]\d))\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{3}[0-9Xx]$)/,
            message: "",
            trigger: "blur"
          }
        ],

        userUniversity: [
          { required: true, message: "", trigger: "blur" }
        ],
        userDepartment: [
          { required: true, message: "", trigger: "blur" }
        ],
        userMajor: [{ required: true, message: "", trigger: "blur" }],
        userEducation: [
          { required: true, message: "", trigger: "blur" }
        ],
        userEnrollmentYear: [
          { required: true, message: "", trigger: "blur" }
        ],
        userMajorCategory: [
          { required: true, message: "", trigger: "blur" }
        ],
        userEducationalSystem: [
          { required: true, message: "", trigger: "blur" }
        ],
        userGraduationTime: [
          { required: true, message: "", trigger: "blur" }
        ],

        guardianName: [
          { required: true, message: "", trigger: "blur" }
        ],
        guardianIdentity: [
          { required: true, message: "", trigger: "blur" },
          { min: 15, max: 18, message: "", trigger: "blur" },
          {
            required: true,
            pattern: /(^[1-9]\d{5}\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{2}$)|(^[1-9]\d{5}(18|19|([23]\d))\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{3}[0-9Xx]$)/,
            message: "",
            trigger: "blur"
          }
        ],
        guardianPermanentAddress: [
          { required: true, message: "", trigger: "blur" }
        ],
        guardianPostalCode: [
          { required: true, message: "", trigger: "blur" }
        ],
        guardianHealth: [
          { required: true, message: "", trigger: "blur" }
        ],
        guardianTel: [
          { required: true, message: "", trigger: "blur" },
          {
            validator: function(rule, value, callback) {
              if (/^((0\d{2,3}-\d{7,8})|(1[34578]\d{9}))$/.test(value) == false) {
                callback(new Error(""));
              } else {
                callback();
              }
            },
            trigger: "blur"
          }
        ],
        relation: [
          { required: true, message: "", trigger: "blur" }
        ]
      }
    };
  },
  mounted() {
    this.$axios
      .post("/user/getUserInfo", {
        userLoginAccount: sessionStorage.getItem("userLoginAccount")
      })
      .then(({ status, data }) => {
        if (status == 200) {
          this.form.userName = data.user.userName;
          this.form.userIdentity = data.user.userIdentity;
          this.form.userPhone = data.user.userPhone;
        }
      });
    this.$axios
      .post("/famousRace/getAllFamousRace")
      .then(({ status, data }) => {
        if (status == 200) {
          this.userFamousRaceList = data;
        }
      });
    this.$axios
      .post("/accountCharacter/getAllAccountCharacter")
      .then(({ status, data }) => {
        if (status == 200) {
          this.accountCharacterList = data;
        }
      });
    this.$axios
      .post("/fundingCenter/getFundingCenterList")
      .then(({ status, data }) => {
        if (status == 200) {
          this.fundingCenterList = data;
        }
      });
    this.$axios.post("/province/getAllProvince").then(({ status, data }) => {
      if (status == 200) {
        this.provinceList = data;
      }
    });
  },
  methods: {
    getCity(key) {
      this.$axios
        .post("/city/getAllCityByProvinceId", {
          provinceId: this.form[key]
        })
        .then(({ status, data }) => {
          if (status == 200) {
            this.cityList = data;
          }
        });
    },
    getArea(key) {
      this.$axios
        .post("/area/getAllAreaByCityId", {
          cityId: this.form[key]
        })
        .then(({ status, data }) => {
          if (status == 200) {
            this.areaList = data;
          }
        });
    },
    submit() {
      
      this.$refs.Form.validate(valid => {
        if (valid) {
          this.$axios
            .post("/user/improveUserInfo", {
              userLoginAccount: sessionStorage.getItem("userLoginAccount"),
              ...this.form
            })
            .then(({ status }) => {
              if (status == 200) {
                this.$message({
                  message: "",
                  type: "success"
                });
                this.$emit('close')
              } else if (status == 401) {
                this.$message({
                  message: "",
                  type: "error"
                });
              }
            })
            .catch(error => {
              console.log(error);
            });
        } else {
          return false;
        }
      });
    }
  }
};
</script>
<style lang="scss" scoped>
.main {
  min-width: 700px;
  max-width: 1000px;
  background: #fff;
  .box {
    padding: 15px 30px;
    .boxTitle {
      text-align: left;
      color: rgb(0, 158, 223);
      font-size: 16px;
      font-weight: bold;
    }
    .boxTitle:not(:first-child) {
      margin-top: 30px;
    }
    .el-form-item {
      margin-bottom: 13px;
      /deep/ .el-form-item__label {
        font-size: 14px;
        font-weight: bold;
      }
      /deep/ .el-form-item__content {
        width: 275px;
        text-align: left;
      }
      
      .selectGround {
        display: flex;
        width: 690px;
        /deep/ .el-select{
          width:250px;
        }
        /deep/ .el-select:nth-child(2), .el-select:nth-child(5) {
          margin:0 10px;
        }
      }
    }
  }
  .submit{
        display: flex;
        justify-content: flex-end;
        margin-top:20px;
        /deep/ .el-form-item__content {
          width: auto!important;
        text-align: right!important;;
      }
      }
}
</style>